import React from 'react';

import getaudio from '../../Common/icons/getaudio.png';
import getvideo from '../../Common/icons/videoinput.png';
import getimgs from '../../Common/icons/getimgs.png';

export default function InputsIcon() {

    return (

        <div className='d-flex flex-md-column mt-4 ps-3 justify-content-evenly'>

                <img className='img-fluid input-icon mt-2' src={getimgs} alt="Icon" />

                <img className='img-fluid input-icon mt-md-5 ' src={getaudio} alt="Icon" />

                <img className='img-fluid input-icon mt-md-5 ' src={getvideo} alt="Icon" />

        </div>

    )
}
