import React, { useState } from 'react';

import Logo from "../Common/Logo/Logo.png";
import mediaSearch from '../Common/icons/searchEngineicon.png';

import InputsPage from './Getinputs/InputsPage';

export default function Welcome() {

    const [inputspage, setInputPage] = useState(false);

    const handleStart = () => {
        setInputPage(InputsPage);

    }

    const setAlignment = {
        height: '100vh',
    }

    return (
        <div className='' style={setAlignment} >

            {
                inputspage ? (
                    <div className=''>
                        <InputsPage Logo={Logo} />
                    </div>
                ) : (
                    <>
                        <div className='d-flex justify-content-center align-items-center Welcomepage'  >

                            <div className="d-flex flex-column mt-5">
                                <img src={Logo} className='img-fluid p-0' width={"200px"} alt="Some Thing is Missing" />

                                <span className='mt-4 m-auto'>
                                    <button onClick={handleStart} className='StartBtn rounded-pill px-5'>Start <i className="fa-solid fa-magnifying-glass"></i></button>
                                </span>

                            </div>


                        </div>

                        <div className='d-none d-md-block ms-3' style={{ position: "absolute", top: "80%" }}>
                            <img src={mediaSearch} className='' style={{ width: "10rem" }} alt="Sorry something went wrong" />
                        </div>

                    </>
                )
            }
        </div>
    )
}

